import React, {Suspense, lazy} from "react";
import logo from '../../assets/images/Logo Ai.png';
import {Navigate, Route, Routes} from "react-router-dom";
const Website = lazy(() => import('../../views/Website'));

const WebsiteRouting = () => (
    <>
        <Suspense fallback={<div className="d-flex justify-content-center align-items-center" style={{height:"100vh", width:"100vw", backgroundColor:"#2b2f3b"}}>
            <img src={logo} alt="logo" />
        </div>}>
            <Routes>
                <Route path="/" element={<Website />}/>
            </Routes>
            {/*<Navigate to="/"  />*/}
        </Suspense>
    </>
);

export default WebsiteRouting;
